<template src="./Main.html"></template>
<style src="./Main.css"></style>
<script>
import { mapGetters, mapActions } from 'vuex';
import moment from 'moment';

export default {
  name: 'Main',
  data() {
    return {
      busy: false,
      systemFeeds: [
        {
          id: 'all',
          name: 'All messages',
          words: '',
          last_retrieve: '',
          new_messages: 0,
        },
        {
          id: 'favorites',
          name: 'Favorites',
          words: '',
          last_retrieve: '',
          new_messages: 0,
        },
        {
          id: 'failover',
          name: 'Fail Over',
          words: '',
          last_retrieve: '',
          new_messages: 0,
        },
      ],
    };
  },
  methods: {
    ...mapActions([
      'addToFavorites',
      'removeFromFavorites',
      'removeAuthorFromBlacklist',
    ]),
    addAuthorToBlacklist(message) {
      this.$store.dispatch('addAuthorToBlacklist', message).then(() => { this.$redrawVueMasonry(); });
    },
    setCurrentFeed(feed) {
      this.busy = true;
      this.$store.dispatch('setCurrentFeed', feed).then(() => { this.busy = false; });
    },
    loadMore() {
      if (!this.currentFeed) return;
      if (this.currentFeed.full_loaded) return;
      this.busy = true;
      this.$store.dispatch(
        'loadMessages',
        {
          feed: this.currentFeed,
          offset: this.messages.length,
        },
      ).then(() => {
        this.busy = false;
      });
    },
    higligthWords(text, words) {
      let txt = text;
      txt = txt.replace(
        new RegExp(
          '(https?://[\\S]+)(?![^<]*>|[^<>]*</)',
          'g',
        ),
        match => `<a href="${match}">${match}</a>`,
      );
      if (!words) {
        return txt;
      }
      const wordArr = [...(function* _(w) {
        const strs = w.split('\r\n');
        for (let i = 0; i < strs.length; i += 1) {
          const wrds = strs[i].split(' ');
          for (let j = 0; j < wrds.length; j += 1) {
            yield wrds[j];
          }
        }
      }(words))];

      for (let i = 0; i < wordArr.length; i += 1) {
        const word = wordArr[i];
        txt = txt.replace(
          new RegExp(`(${word})(?![^<]*>|[^<>]*</)`, 'gi'),
          match => `<span class="highlighted_word">${match}</span>`,
        );
      }
      return txt;
    },
    prettyDate(datestr) {
      const date = moment.parseZone(datestr);
      return date.format('DD.MM.YYYY, HH:mm:ss');
    },
  },
  computed: {
    ...mapGetters(
      [
        'feedList',
        'currentFeed',
        'messages',
      ],
    ),
  },
  created() {
    this.$store.dispatch('getFeedList').then(() => {});
    this.setCurrentFeed(this.systemFeeds[0]);
  },
};
</script>



