export default {

  addAuthorToBlacklist(message) {
    return fetch(`api/feed_message/blacklist/${message}/`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'same-origin',
    })
      .then(response => response.json());
  },
  removeAuthorFromBlacklist(message) {
    return fetch(`api/feed_message/blacklist/${message}/remove`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'same-origin',
    })
      .then(response => response.json());
  },
  addToFavorites(message) {
    return fetch(`/api/feed_message/favorites/${message}/`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'same-origin',
    })
      .then(response => response.json());
  },
  removeFromFavorites(message) {
    return fetch(`/api/feed_message/favorites/${message}/remove`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'same-origin',
    })
      .then(response => response.json());
  },
  getMessages(feed, offset = 0) {
    return fetch(`/api/feed_message/${feed}/?offset=${offset}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'same-origin',
    })
      .then(response => response.json());
  },
  getFeedList() {
    return fetch('/api/feed', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'same-origin',
    })
      .then(response => response.json());
  },
  getFavorites() {
    return fetch('/api/favorite', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'same-origin',
    })
      .then(response => response.json());
  },
  getSources() {
    return fetch('/api/source', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'same-origin',
    })
      .then(response => response.json());
  },
};



